@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;400;600;700;900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,300;1,400;1,600;1,700&display=swap');

*{
    margin: 0;
    padding: 0;
}

/* NAVBAR  */


.page{
    text-decoration: none;
    color: black;
}

.navbar{
    display: flex;
    height: 133px;
    justify-content: space-between;
    padding-left: 5%;
    padding-right: 5%;
    background-color: #fffbf5;
}

.logo{
    /* border: 2px solid red; */
    width: 162px;
    height: 133px;
}

.pages{
    /* border: 2px solid blue; */
    width: 45%;
    display: flex;
    font-size: 1.5rem ;
    justify-content: space-between;
    align-items: center;
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
}

@media (max-width: 1100px){

    .pages{
        width: 65%;
    }    
}

@media (max-width: 460px){

    .navbar{
        height: 100px;
        width: 100%;
        padding-left: 0%;
        padding-right: 0%;
    }
    
    .logo{
        width: 80px;
        height: 80px;
        margin-top: 10px;
    }
    .pages{
        width: 70%;
        font-family: 'Roboto', sans-serif;
        margin-right: 5px;
    }
    .page{
        text-decoration: none;
        color: black;
        font-size: 1rem;
    }
    
} 


